<script>
    import { cubicInOut, quintInOut } from 'svelte/easing';
    import { tweened } from 'svelte/motion';
    import * as animateScroll from "svelte-scrollto";
    export let height;
    
    const tweenConfig1 = {
        duration: 500,
        easing: cubicInOut
    };
    const tweenConfig2 = {
        duration: 500,
        easing: cubicInOut,
        delay:20
    };
    const tweenConfig3 = {
        duration: 500,
        easing: cubicInOut,
        delay:40
    };
    const barPosition1 = tweened(0, tweenConfig1);
    const barPosition2 = tweened(0, tweenConfig2);
    const barPosition3 = tweened(0, tweenConfig3);

    let menus = [
        {menu:'Jurno',id:0, active:true, link:"#jurno"},
        {menu:'Original',id:1, active:false, link:"#original"},
        {menu:'Deduktif',id:2, active:false, link:"#deduktif"},
        {menu:'Podcast',id:3, active:false, link:"#podcast"}
    ]

    const menuClick = (id) => {
        menus.forEach((menu) => {
            menu.active = false;
        })
        menus[id].active = true;

        animateScroll.scrollTo({element: menus[id].link, offset: -150})

        barPosition1.set(id)
        barPosition2.set(id)
        barPosition3.set(id)
    }
   
</script>

<nav>
    <div class="container" bind:clientHeight={height}>
        <div class="header-logo">
            <img src="./image/logo-jurno-web.svg" alt="Logo Jurno Website">
        </div>
        <div class="header-menu">
            <div class="headertext">
                {#each menus as list, key}
                    <h4 class="menu"
                        id={list.id}
                        on:click={()=> {menuClick(key)}}
                        class:selected ="{list.active === true}"
                    >
                        {list.menu}
                    </h4>
                {/each}
            </div>
            <div class="bar" style="left:{$barPosition1*25+8.5}vw"></div>
            <div class="bar" style="left:{$barPosition2*25+8.5}vw"></div>
            <div class="bar" style="left:{$barPosition3*25+8.5}vw"></div>
        </div>
    </div>
    
</nav>

<style>
    nav {
        z-index: 999;
    }
    .header-logo{
        width: 100%;
        display: flex;
    }
    img {
        margin:0.5rem auto;
        height:3.5rem;
    }
    .container {
        height:30vw;
        z-index: 99;
        position: fixed;
        background-color: var(--color-brand-white);
        width:100vw;
    }
    .headertext {
        display:flex;
        justify-content: center;
        color:#B9B5A8;
    }
    .selected {
        color:black;
    }
    .menu {
        width:25vw;
        text-align: center;
        color:var(--color-brand-red);
    }
    .header-menu {
        display: flex;
        flex-direction: column;
    }

    .bar {
        width:12.5vw;
        height:0.25rem;
        background-color: var(--color-brand-blue);
        border-radius: 0.25rem;
        display: block;
        position: absolute;
        z-index: 200;
        top: 6.5rem;
        left: 8.5vw;
        width: 10vw;
        pointer-events: none;
    }
    .bar:nth-child(1) {
		transition: calc(500 * .8);
	}
	.bar:nth-child(2) {
		transition: calc(500 * 1.2);
	}

    h4 {
        font-family: var(--fontfamily3);
        font-size:1rem;
        font-weight:700; 
        margin: 0.2rem 1rem;
    }
</style>